
import React, { useEffect, memo } from "react";
import styled from "styled-components";
import { sidePadding, mainWrapper, topPaddingLarge } from "../../../styles/wrapper";
import colors from "../../../constants/colors";
import { CreateBgColor } from "../../../containers";
import time from "../../../constants/time";

export interface MenusProps {
}

/**
 * Styled component.
 */
const Main = styled.div`
  height: 100%;
  position: relative;
  background-color: ${ colors.blue };
  ${ sidePadding }
  ${ topPaddingLarge }
  padding-bottom: 100px;
  & > div {
    ${ mainWrapper }
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const Menu: React.FC<MenusProps> = memo(({
  children
}) => {

  const { setBgColor } = CreateBgColor.useContainer();

  useEffect(() => {
    setTimeout(() => { setBgColor(colors.blue); }, time.transitionAnimation);
  }, []);

  return (
    <>
      <Main>
        { children }
      </Main>
    </>
  );
});
